import empty from "@/components/empty";
import Json from '@/Json';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [
				{
					state: 0,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '已完成',
					loadingType: 'more',
					orderList: []
				}
			],
			orderList:[],
			
			source: 0,
			addressList: [],
			scrollHeight: 100,
			tabScrollTop: 0,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			page:1,
			limit:20,
			status: 'more',
		};
	},
	
	onLoad(options){
		
		this.tabCurrentIndex = options.state
		this.page = 1
		this.limit = 20
		this.orderList = []
		this.getUserOrderList();
		// this.tabCurrentIndex = 0
		console.log(options,'sdfd',this.tabCurrentIndex);
		this.calcSize();
	},
	onShow(){
		
	},
	 
	methods: {
		getUserOrderList(type){
			let self = this;
			
			console.log(type,'哈哈哈哈哈哈哈',this.tabCurrentIndex)
			let path = '/gzh/user_api/get_user_order_list'
			let data = {
				type: type ? type : this.tabCurrentIndex,
				page:self.page,
				limit:self.limit
			}
			getApp().http(path, data, function(res) {
				uni.hideLoading();
				let list = res;
				if (list.length > 0) {
					console.log(list.length)
					self.orderList = self.orderList.concat(list);
					self.status = list.length >= self.limit ? "more" : "";
					if (self.status == "more") {
						self.page += 1;
					}
				} else {
					self.status = "";
				}
				
				console.log('水电费水电费',self.orderLis);
			})
		},
		//顶部tab点击
		tabClick(index,item){
			let self = this;
			self.tabCurrentIndex = index;
			let type = item.state
			
			uni.showLoading({})
			
			self.orderList = [];
			self.page = 1;
			self.getUserOrderList(type);
			console.log('点击',this.tabCurrentIndex,type)
			
		},
		orderdet(item){
			console.log('详情',item)
			uni.navigateTo({
				url: "/pages/order/orderDetail?uni="+item.order_id
			})
		},
		// 立即付款
		orderzf(item){
			console.log('支付',item)
			uni.navigateTo({
				url: "/pages/order/orderDetail?uni="+item.order_id
			})
		},
		
		ordert(item){
			console.log('支付',item)
			uni.navigateTo({
				url: "/pages/order/orderT?uni="+item.order_id
			})
		},
		
		//取消订单
		cancelOrder(item){
			uni.showLoading({})
			let self = this;
			let path = '/gzh/auth_api/cancel_order'
			let data = {
				order_id: item.order_id
			}
			getApp().http(path, data, function(res) {
				uni.hideLoading();
				if(res.code == 200){
					self.getUserOrderList('0');
					getApp().showTip(res.msg, "success")
				}else{
					getApp().showTip(res.msg, "none")
				}
			},true)
		},
		
		orderEval(item){
			
		},
		
		indexUrl(icon) {
			let http = icon.indexOf('http')
			if (http == -1) {
				return this.webUrl + '/' + icon
			} else {
				return icon;
			}
		},
		
		// 到底触发
		scroAddr(){
			let self = this;
			if(self.status){
				self.getUserOrderList();
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			var self = this;
			uni.getSystemInfo({
				success: function(res) { // res - 各种参数
					let info = uni.createSelectorQuery().select(".content");
					self.scrollHeight = res.windowHeight - 40
					// self.scrollHeight = 300
					console.log(info);
					console.log('啊啊啊啊',res);
				}
			});
		},
		
		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		
		//删除订单
		deleteOrder(index){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		

	},
}
